router.get('/dd', async (req, res) => {
	const betOrders = await BetOrder.find({ isClosed: false }).populate({path: 'Picks', populate: { path: 'Event' }});
	const picks = betOrders.map(betOrder => betOrder.Picks.map(pick => pick));
	res.json(_.flatten(picks));
});

